import * as React from "react";
import {
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  Stack,
  Grid,
  Paper,
  Typography,
} from "@mui/material";
import PuffLoader from "react-spinners/PuffLoader";
import AddBoxOutlinedIcon from "@mui/icons-material/AddBoxOutlined";
import {
  Chart,
  PieSeries,
  BarSeries,
  Title,
  ArgumentAxis,
  Tooltip,
  ValueAxis,
  Legend,
} from "@devexpress/dx-react-chart-material-ui";
import { EventTracker } from "@devexpress/dx-react-chart";
import axios from "../../axios";
import { Animation } from "@devexpress/dx-react-chart";
import { easeBounceOut } from "d3-ease";
import { connect } from "react-redux";
const mapStateToProps = (state) => ({
  collegeID: state.collegeID,
});
class StudentChart extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      data1: [],
      data2: [],
      data3: [],
      total: "",
      batchlist: [],
      batch: "",
      loader: false,
      semList: [],
      semester: "",
    };
  }
  handleChange = (event) => {
    this.setState({
      batch: [event.target.value],
    });
    axios
      .get(
        "api/adminerp/semester/list/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        this.setState({
          semList: res.data,
          data: [],
          data1: [],
          data2: [],
          data3: [],
        });
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  };
  handleSemChange = (event) => {
    this.setState({
      semester: [event.target.value],
    });
    this.setState({
      loader: true,
    });

    axios
      .get(
        `api/admission/count/category/` +
          this.state.batch +
          "/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        const rdata = res.data;
        this.setState({
          data: rdata,
        });
      })
      .catch((error) => {
        console.log(error);
      });
    axios
      .get(
        `api/admission/count/semester/` +
          this.state.batch +
          "/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        const sem = res.data;
        this.setState({
          data1: sem,
        });
      })
      .catch((error) => {
        console.log(error);
      });
    axios
      .get(
        `api/admission/count/coursename/` +
          this.state.batch +
          "/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        const total = res.data;
        this.setState({
          data2: total,
        });
      })
      .catch((error) => {
        console.log(error);
      });
    axios
      .get(
        `api/admission/count/gender/` +
          this.state.batch +
          "/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        const gender = res.data;
        this.setState({
          data3: gender,
        });
      })
      .catch((error) => {
        console.log(error);
      });
    axios
      .get(
        `api/admission/count/total/` +
          this.state.batch +
          "/" +
          event.target.value +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        const total1 = res.data[0];
        for (var key in total1) {
          var value = total1[key];
          this.setState({
            total: value,
          });
          this.setState({
            loader: false,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  componentDidMount() {
    axios
      .get("api/adminerp/batch/list/" + this.props.collegeID)
      .then((res) => {
        if (res.status === 200) {
          this.setState({
            batchlist: res.data,
          });
        }
      })
      .catch(function (error) {
        alert("Error occured:" + error);
      });
  }
  render() {
    // const { data: chartData } = this.state;

    return (
      <Grid container spacing={3}>
        <Grid item lg={6} xs={12} sm={12} md={12}>
          <Stack sx={{ width: "80%" }} spacing={2}>
            <h4>
              <AddBoxOutlinedIcon className="mr-2" />
              {"Select Student -> Batch"}
            </h4>
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Batch</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={this.state.batch}
                label="Batch "
                onChange={this.handleChange}
              >
                {this.state.batchlist.map((item, index) => (
                  <MenuItem key={index} value={item.Batch}>
                    {item.Batch}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Stack>
        </Grid>
        <Grid item lg={6} xs={12} sm={12} md={12}>
          <Stack sx={{ width: "80%" }} spacing={2}>
            <h4>
              <AddBoxOutlinedIcon className="mr-2" />
              {"Select Student -> Semester"}
            </h4>
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Semester</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={this.state.semester}
                label="Semester "
                onChange={this.handleSemChange}
              >
                {this.state.semList.map((item, index) => (
                  <MenuItem key={index} value={item.Sem_ID}>
                    {item.Sem_Name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Stack>
        </Grid>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            marginTop: "50px",
            backgroundColor: "#fff",
          }}
        >
          {this.state.loader ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginTop: "50px",
                backgroundColor: "#fff",
              }}
            >
              <PuffLoader color="#283593" size="80" />
              Loading Please wait...
            </div>
          ) : (
            <>
              <Grid container spacing={3}>
                <Grid item xs={12} lg={12}>
                  <Typography variant="h4" color="primary">
                    Total Candidates Enrolled :
                    {JSON.stringify(this.state.total)}
                  </Typography>
                </Grid>
                <Grid item xs={12} lg={6}>
                  <Paper>
                    <Chart
                      data={this.state.data}
                      height={600}
                      //rotated={true}
                    >
                      <PieSeries
                        valueField="count(category)"
                        argumentField="category"
                        innerRadius={0.5}
                      />
                      <Title text="Category Wise" position="top" />
                      <Animation duration={4000} easing={easeBounceOut} />
                      <Legend />
                      <EventTracker />
                      <Tooltip />
                    </Chart>
                  </Paper>
                </Grid>
                <Grid item xs={12} lg={6}>
                  <Paper>
                    <Chart data={this.state.data2} height={400} rotated={true}>
                      <ArgumentAxis />
                      <ValueAxis showGrid={false} showLine showTicks />
                      <BarSeries
                        valueField="count(Course_name)"
                        argumentField="Course_name"
                        barWidth={1.1}
                      />
                      <Title text="Course Wise" />
                      <Animation duration={4000} easing={easeBounceOut} />
                      <EventTracker />
                      <Tooltip />
                    </Chart>
                  </Paper>
                </Grid>
                {/*  <Grid item xs={12} lg={12}>
                <Paper>
                  <Chart data={this.state.data1} height={1500} rotated={true}>
                    <ArgumentAxis />
                    <ValueAxis showGrid={true} showLine showTicks />
                    <BarSeries
                      valueField="Total"
                      argumentField="Subject1"
                      barWidth={1.3}
                      color="#8E44AD "
                    />
                    <Title text="Subject Wise" />
                    <EventTracker />
                    <Tooltip />
                    <Animation duration={4000} easing={easeBounceOut} />
                  </Chart>
                </Paper>
              </Grid> */}
                <Grid item xs={12} lg={6}>
                  <Paper>
                    <Chart data={this.state.data3} height={300}>
                      <PieSeries
                        valueField="count(gender)"
                        argumentField="gender"
                        innerRadius={0.5}
                      />

                      <Title text="Gender Wise" />
                      <EventTracker />
                      <Tooltip />
                      <Legend />
                      <Animation duration={4000} easing={easeBounceOut} />
                    </Chart>
                  </Paper>
                </Grid>
              </Grid>
            </>
          )}
        </div>
      </Grid>
    );
  }
}
export default connect(mapStateToProps)(StudentChart);
